@import url("https://fonts.googleapis.com/css2?family=Cardo:wght@700&family=Commissioner:wght@700&family=Montserrat:wght@300;500;700&family=Raleway:wght@300;500;700&display=swap");
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu",
    "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

header {
  width: 1100px;
  margin-top: 51px;
  display: flex;
  margin-left: auto;
  margin-right: auto;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 1;
  background-color: rgba(0, 0, 0, 0.6);
  padding-top: 30px;
  color: #fff;
  z-index: 99;
}
header .logo {
  width: 20%;
}
header .logo span {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: #fff;
  position: relative;
  top: -3px;
  left: 10px;
}
header .logo .logotip {
  height: 20px;
}
header .menu {
  width: 60%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex-wrap: wrap;
}
header .menu ul {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  padding-left: 0%;
  position: relative;
  bottom: 15px;
}
header .menu ul li {
  width: 25%;
  cursor: pointer;
  list-style: none;
  padding-left: 0;

  font-family: "Raleway";
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 140%;
  letter-spacing: 0.02em;
  transition: 0.5s;
  color: #fff;
  flex: none;
  order: 0;
  flex-grow: 0;
}
header .menu ul li:hover {
  color: #6e9c9f;
}
header .contacts {
  width: 19.55%;
  display: flex;
  justify-content: end;
}
header .contacts .tell {
  width: 15px;
  position: relative;
  top: 5px;
  color: red;
}
header .contacts span {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 140%;
  letter-spacing: 0.02em;
  color: #fff;
  margin-left: 2px;
  margin-right: 40px;
}
header .contacts .basket {
  width: 25px;
  height: 25px;
  position: relative;
  top: -3px;
}
header .contacts .basket,
header .contacts span,
header .contacts .tell {
  cursor: pointer;
  transition: 0.5s;
}
header .contacts .basket:hover,
header .contacts span:hover {
  color: #6e9c9f;
}

footer {
  background: #f1eadc;
  width: 100%;
  height: 363px;
}
footer .footerMain {
  width: 1100px;
  display: flex;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  top: 30%;
}
footer .logo {
  width: 20%;
  position: relative;
  top: 25px;
}
footer .logo .logotxt {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: #000000;
  position: relative;
  top: -3px;
  left: 10px;
}
footer .logo .logotip {
  height: 20px;
}
footer .logo div {
  width: 100%;
  margin-top: 50px;
}
footer .logo div span {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 400;
  font-size: 13px;
  line-height: 140%;
  letter-spacing: 0.02em;
  color: #000000;
  flex: none;
  order: 2;
  flex-grow: 0;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}
footer .menu {
  width: 60%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex-wrap: wrap;
}

footer .menu .listLiGlav {
  width: 25%;
  cursor: pointer;
  list-style: none;
  padding-left: 0;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  align-items: center;
  height: 60%;
  position: relative;
  font-family: "Raleway";
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 140%;
  letter-spacing: 0.02em;
  transition: 0.5s;
  color: #000000;
  flex: none;
  order: 0;
  flex-grow: 0;
}
footer .menu .listLiGlav:hover {
  color: #6e9c9f;
}

footer .menu .menuOut {
  margin-top: 50px;
  cursor: pointer;
  display: flex;
  flex-wrap: wrap;
  justify-content: start;
  width: 40px;
  position: absolute;
  left: 39.5%;
  top: 40%;
}

footer .menu .menuOut .listLiGlavOut {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 400;
  font-size: 13px;
  line-height: 140%;
  letter-spacing: 0.02em;
  color: #000000;
  transition: 0.5s;
  width: 60px;
  margin-top: 2px;
  margin-left: 3px;
  display: flex;
  justify-content: start;
}
footer .menu .menuOut .listLiGlavOut:hover {
  color: #6e9c9f;
}

footer .contacts {
  width: 20%;
  display: flex;
  justify-content: end;
  flex-wrap: wrap;
  position: relative;
  top: 30px;
}
footer .contacts .tell {
  width: 25px;
  position: relative;
  top: 5px;
}
footer .contacts span {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 140%;
  letter-spacing: 0.02em;
  color: #000000;
}
footer .contacts .basket {
  width: 25px;
  height: 25px;
  position: relative;
  top: -3px;
}

footer .contacts .basket,
footer .contacts span,
footer .contacts .tell {
  cursor: pointer;
  transition: 0.5s;
}
footer .contacts .tell {
  color: #6e9c9f;
}
footer .contacts .basket:hover,
footer .contacts span:hover {
  color: #6e9c9f;
}
footer .contacts div {
  display: flex;
  justify-content: flex-end;
  flex-wrap: wrap;
}
footer .contacts div .weOnWebsite {
  width: 22px;
  height: 22px;
  transition: 0.5s;
  cursor: pointer;
  margin: 4px;
}
footer .contacts div .weOnWebsite:hover {
  color: #6e9c9f;
}

.imgItem {
  width: 350px;
  height: 478px;
  transition: 0.5s;
}
.imgItem:hover {
  background: rgba(110, 156, 159, 0.64);
}
.titleItem {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 140%;
  letter-spacing: 0.02em;
  color: #000000;
  flex: none;
  order: 0;
  flex-grow: 0;
}
.priceItem {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  line-height: 140%;
  letter-spacing: 0.02em;
  color: #998e78;
  flex: none;
  order: 1;
  flex-grow: 0;
}
main {
  padding-top: 50px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 1100px;
  min-height: 400px;
  margin: auto;
}
main .item {
  width: 350px;
  height: 450px;
  text-align: center;
}
main .item .add {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: #fff;
  color: #000000;
  border: 1px solid black;
  display: flex;
  justify-content: center;
  text-align: center;
  align-items: center;
  position: relative;
  top: -50px;
  font-size: 25px;
  cursor: pointer;
  transition: 0.5s;
  z-index: 10;
}
main .item .add span {
  position: relative;
  top: -4px;
}
main .item .add:hover {
  color: #fff;
  background-color: #000000;
}
main .item .imgItem {
  width: 100%;
  height: 350px;
  cursor: pointer;
}
main .item .titleItem {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 140%;
  letter-spacing: 0.02em;
  color: #000000;
  flex: none;
  order: 0;
  flex-grow: 0;
  width: 50%;
  cursor: pointer;
}
main .item .priceItem {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  line-height: 140%;
  letter-spacing: 0.02em;
  color: #998e78;
  flex: none;
  order: 1;
  flex-grow: 0;
  width: 50%;
  cursor: pointer;
}

.shop-cart {
  width: 450px;
  min-height: 100px;
  background-color: #6e9c9f;
  position: absolute;
  top: 100%;
  left: 57.3%;
  -webkit-box-shadow: 5px 5px 15px 5px rgba(0, 0, 0, 0.21);
  box-shadow: 5px 5px 15px 5px rgba(0, 0, 0, 0.21);
  padding: 10px;
  z-index: 1;
}
.shop-cart .item {
  width: 100%;
  display: flex;
  justify-content: start;
  align-items: center;
  text-align: center;
  flex-wrap: wrap;
  margin-bottom: 20px;
}
.shop-cart .item img {
  width: 100px;
  height: 110px;
  float: left;
  margin-right: 20px;
}
.shop-cart .item .titleItem {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 140%;
  letter-spacing: 0.02em;
  color: #fff;
  flex: none;
  order: 0;
  flex-grow: 0;
}
.shop-cart .item .priceItem {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  line-height: 140%;
  letter-spacing: 0.02em;
  color: #fff;
  flex: none;
  order: 1;
  flex-grow: 0;
}
.delete-icon {
  position: relative;
  left: 60px;
  width: 20px;
  height: 20px;
  transition: 0.5s;
  cursor: pointer;
}
.delete-icon:hover {
  color: black;
  transform: rotate(180deg);
}

.empty h3 {
  font-family: "Raleway";
  letter-spacing: 0.08em;
}
.active {
  color: red;
}
.SHOP {
  margin-top: 200px;
  margin-bottom: 200px;
  font-family: "Raleway";
  font-style: normal;
  font-weight: 500;
  font-size: 55px;
  line-height: 110%;
  letter-spacing: 0.02em;
  color: #000000;
  flex: none;
  order: 0;
  flex-grow: 0;
}
.summa {
  font-family: "Raleway";
  font-size: 20px;
}
.osnova {
  width: 1100px;
  margin: auto;
}
.categories {
  display: flex;
  justify-content: flex-start;
}
.categories div {
  border: 1px solid black;
  width: 162px;
  height: 62px;
  background: #fff;
  color: #000000;
  margin-right: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  transition: 0.5s;
  cursor: pointer;
}
.categories div span {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 500;
  font-size: 17px;
  line-height: 140%;
  letter-spacing: 0.02em;
  color: #000000;
  flex: none;
  order: 0;
  flex-grow: 0;
}
.categories div:hover {
  background-color: #000000;
}
.categories div:hover span {
  color: #fff;
}

.full-item {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.8);
  z-index: 9999;
  overflow: auto;
}
.full-item > div {
  width: 600px;
  position: relative;
  margin: 10% auto;
  padding: 40px 30px;
  background-color: #fff;
  border-radius: 9px;
  display: flex;
  justify-content: space-around;
  text-align: center;
  flex-wrap: wrap;
}
.full-item > div .imgItem {
  cursor: pointer;
}

.full-item > div .imgItem {
  width: 200px;
  height: 200px;
  border-radius: 50%;
}
.full-item > div .titleItem {
  margin-top: 15px;
  width: 100%;
  height: 50px;
}
.full-item > div .textItem {
  width: 90%;
  min-height: 200px;
}

.full-item .add {
  width: 80px;
  height: 80px;
  background-color: #fff;
  border: 1px solid black;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: 40px;
  transition: 0.5s;
  cursor: pointer;
}
.full-item .add span {
  font-size: 25px;
}
.full-item .add:hover {
  background-color: #000000;
  color: #fff;
}
.exit {
  width: 40px;
  height: 40px;
  background-color: #000000;
  color: #fff;
  transition: 0.5s;
  position: absolute;
  margin: -20px;
  top: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  border-radius: 50%;
  border: 2px solid black;
  cursor: pointer;
}
.exit .exitOut {
  width: 70%;
  height: 70%;
}
.exit:hover {
  background-color: #fff;
  color: #000000;
}

@media (max-width: 1150px) {
  header {
    width: 800px;
    margin-left: auto;
    margin-right: auto;
  }

  header .logo {
    width: 20%;
  }

  header .menu {
    width: 50%;
  }
  header .contacts span {
    display: none;
  }
  header .contacts .basket {
    width: 25px;
    height: 25px;
  }

  .categories div {
    width: 152px;
    height: 55px;
  }

  .SHOP {
    margin-top: 150px;
    margin-bottom: 150px;
  }

  main {
    width: 800px;
    min-height: 400px;
  }
  main .item {
    width: 250px;
    height: 400px;
  }
  main .item .imgItem {
    width: 100%;
    height: 250px;
  }
  main .item .add {
    width: 40px;
    height: 40px;
  }

  footer {
    width: 100%;
    height: 330px;
  }
  footer .footerMain {
    width: 800px;
    justify-content: space-between;
  }
  footer .logo,
  footer .contacts {
    width: 25%;
  }
  footer .menu {
    display: none;
  }

  .shop-cart {
    width: 380px;
    min-height: 80px;
    left: 50%;
  }

  .osnova {
    width: 800px;
  }
}

