    width: 10%;
  }
  header .contacts span {
    display: none;
  }
  header .contacts .basket {
    width: 25px;
    height: 25px;
  }

  .categories div {
    width: 132px;
    height: 45px;
  }

  .SHOP {
    margin-top: 170px;
    margin-bottom: 90px;
  }

  main {
    width: 600px;
    min-height: 500px;
    justify-content: space-between;
  }
  main .item {
    width: 190px;
    height: 300px;
  }
  main .item .imgItem {
    width: 100%;
    height: 190px;
  }
  main .item .titleItem {
    font-size: 15px;
  }
  main .item .add {
    margin-top: 10px;
