  footer {
    width: 100%;
    height: 330px;
  }
  footer .footerMain {
    width: 600px;
  }

  footer .menu {
    display: none;
  }
  footer .logo,
  footer .contacts {
    width: 25%;
  }

  .shop-cart {
    width: 220px;
    min-height: 80px;
    left: 60%;
  }
}
