  }
  .shop-cart div .item .imgItem {
    width: 100px;
    height: 100px;
  }
  .shop-cart div .item span {
    display: none;
  }

  .delete-icon {
    position: relative;
    left: 1px;
    width: 15px;
    height: 15px;
  }
  .summa {
    font-size: 15px;
  }
}

